/*!
 * Theme Name: YP
 */
@import "destyle.css/destyle.css";
@import "magnific-popup/dist/magnific-popup.css";
@font-face {
  font-family: 'Franklin Gothic Demi Cond';
  src: url("fonts/FranklinGothic-DemiCond.eot");
  src: url("fonts/FranklinGothic-DemiCond.eot?#iefix") format('embedded-opentype'), url("fonts/FranklinGothic-DemiCond.woff2") format('woff2'), url("fonts/FranklinGothic-DemiCond.woff") format('woff'), url("fonts/FranklinGothic-DemiCond.ttf") format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Carefree Cyrillic';
  src: url("fonts/CarefreeCyrillic.eot");
  src: url("fonts/CarefreeCyrillic.eot?#iefix") format('embedded-opentype'), url("fonts/CarefreeCyrillic.woff2") format('woff2'), url("fonts/CarefreeCyrillic.woff") format('woff'), url("fonts/CarefreeCyrillic.ttf") format('truetype');
  font-weight: normal;
  font-style: normal;
}
.w {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
  max-width: 1340px;
  flex-direction: column;
}
@media (min-width: 900px) {
  .w {
    flex-direction: row;
  }
}
.c {
  padding: 0 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1340px;
}
html {
  font-family: 'Open Sans', sans-serif;
  min-height: 100%;
}
.hidden {
  display: none;
}
.screen-reader-response {
  display: none;
}
.wpcf7-not-valid-tip {
  position: absolute;
  font-size: 14px;
  color: #f00;
  top: 100%;
  margin-top: 4px;
}
.wpcf7-response-output {
  font-size: 15px;
  margin-top: 8px;
  text-align: center;
  color: #f00;
}
.wpcf7-response-output.wpcf7-mail-sent-ok {
  color: #1caf5e;
}
.header {
  display: flex;
  flex-direction: row;
  height: 83px;
  align-items: center;
}
@media (max-width: 899px) {
  .header {
    padding: 0;
    flex-direction: column-reverse;
    align-items: stretch;
    height: auto;
  }
}
.header__logo {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
}
@media (max-width: 899px) {
  .header__logo {
    flex: 0 auto;
    height: 55px;
    padding: 0 21px;
    justify-content: space-between;
  }
}
.header__img {
  height: 100%;
}
@media (max-width: 899px) {
  .header__img {
    width: 61px;
  }
}
.header__info {
  font-size: 16px;
  color: #363940;
  margin-left: 18px;
}
@media (max-width: 899px) {
  .header__info {
    font-size: 12px;
    margin-left: 0;
  }
}
.header__contacts {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 16px;
  color: #2d2d2d;
}
@media (max-width: 899px) {
  .header__contacts {
    flex: 0 auto;
    align-items: center;
    background: #e6e6e6;
    height: 27px;
    font-size: 10px;
    padding: 0 21px;
  }
}
.header__phone:hover {
  text-decoration: underline;
}
.header__phone:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: url("img/phone.png");
  width: 29px;
  height: 29px;
  background-size: 29px 29px;
  margin-right: 21px;
}
@media (max-width: 899px) {
  .header__phone:before {
    width: 12px;
    height: 12px;
    background-size: 12px 12px;
    margin-right: 6px;
  }
}
.header__email:hover {
  text-decoration: underline;
}
.header__email:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: url("img/letter.png");
  width: 31px;
  height: 31px;
  background-size: 31px 31px;
  margin-right: 21px;
}
@media (max-width: 899px) {
  .header__email:before {
    width: 12px;
    height: 12px;
    background-size: 12px 12px;
    margin-right: 6px;
  }
}
.slide {
  background: #f2f2f2;
  background-image: url("img/bg.jpg");
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
@media (max-width: 899px) {
  .slide {
    background-size: cover;
  }
}
.slide__wrapper {
  display: flex;
  padding: 120px 10px 130px;
}
@media (max-width: 899px) {
  .slide__wrapper {
    flex-direction: column;
    padding: 18px 32px 20px;
  }
}
.slide__content {
  flex: 1;
}
.slide__title {
  font-family: 'Franklin Gothic Demi Cond', sans-serif;
  font-weight: normal;
  font-size: 67px;
  line-height: 0.8;
  margin: 0 0 12px;
}
@media (max-width: 899px) {
  .slide__title {
    font-size: 21px;
    text-align: center;
  }
}
.slide__subtitle {
  font-size: 32px;
  font-weight: 300;
  line-height: 0.9;
  color: #191919;
  margin: 0 0 160px;
}
@media (max-width: 899px) {
  .slide__subtitle {
    font-size: 11px;
    text-align: center;
    margin-bottom: 18px;
  }
}
.slide__cta {
  font-family: 'Carefree Cyrillic', sans-serif;
  color: #00b7f4;
  padding-left: 60px;
  display: flex;
  align-items: center;
}
@media (max-width: 899px) {
  .slide__cta {
    padding-left: 0;
    justify-content: center;
    margin-bottom: 13px;
  }
}
.slide__cta:after {
  content: '';
  display: block;
  flex: 1;
  height: 200px;
  background-image: url("img/arrow1.png");
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  max-width: 450px;
}
@media (max-width: 899px) {
  .slide__cta:after {
    background-image: url("img/arrow2.png");
    max-width: 89px;
    height: 40px;
  }
}
.slide__text {
  flex: 0 auto;
  margin: 0;
  font-size: 43px;
  line-height: 0.65;
  align-self: center;
}
@media (max-width: 899px) {
  .slide__text {
    font-size: 21px;
  }
}
.form {
  width: 100%;
  max-width: 430px;
  background: #373941;
  padding: 42px 63px 34px 40px;
  align-self: center;
  margin: 0 auto;
  position: relative;
}
@media (max-width: 899px) {
  .form {
    padding: 24px 37px 20px 24px;
  }
}
.form .mfp-close {
  color: #fff;
}
.input {
  margin-bottom: 25px;
}
@media (max-width: 899px) {
  .input {
    margin-bottom: 15px;
  }
}
.input__input {
  display: block;
  width: 100%;
  border: 1px solid #00b6f4;
  outline: 0;
  font-size: 16px;
  color: #2d2d2d;
  padding: 12px 30px;
  border-radius: 8px;
  background: #fff;
  transition: all 0.1s ease-out;
}
@media (max-width: 899px) {
  .input__input {
    font-size: 10px;
    padding: 6px 15px;
  }
}
.input__submit {
  background: #00b6f4;
  color: #fff;
  font-size: 25px;
  line-height: 1;
  text-align: center;
  width: 100%;
  display: block;
  border-radius: 8px;
  padding: 23px 0;
  transition: all 0.1s ease-out;
}
.input__submit:hover {
  background: #00a4dc;
}
@media (max-width: 899px) {
  .input__submit {
    font-size: 14px;
    padding: 14px 0;
  }
}
.input__error {
  position: absolute;
  font-size: 13px;
  color: #ff5b38;
  margin-top: 2px;
}
@media (max-width: 899px) {
  .input__error {
    font-size: 9px;
  }
}
.action {
  display: flex;
  align-items: center;
  padding-top: 45px;
  padding-bottom: 45px;
}
@media (max-width: 899px) {
  .action {
    flex-direction: column;
    text-align: center;
    padding-top: 12px;
    padding-bottom: 28px;
  }
}
.action__text {
  flex: 1;
  font-size: 37px;
  color: #363940;
  font-family: 'Franklin Gothic Demi Cond', sans-serif;
  padding-right: 12px;
}
@media (max-width: 899px) {
  .action__text {
    font-size: 16px;
    padding: 0 30px;
    max-width: 400px;
    margin-bottom: 12px;
  }
}
.action__button {
  border-radius: 8px;
  border: 2px solid #902914;
  font-size: 25px;
  color: #902914;
  padding: 20px 45px;
  background: #fff;
  transition: all 0.1s ease-out;
}
.action__button:hover {
  background: #902914;
  color: #fff;
}
@media (max-width: 899px) {
  .action__button {
    font-size: 15px;
    padding: 10px 15px;
  }
}
.bullets {
  display: flex;
  justify-content: space-around;
  margin-bottom: 75px;
}
@media (max-width: 899px) {
  .bullets {
    flex-direction: column;
    align-items: center;
    margin: 0 auto 32px;
    width: 320px;
    align-items: flex-start;
  }
}
.bullets__item {
  height: 151px;
  position: relative;
  display: flex;
  align-items: flex-end;
}
@media (max-width: 899px) {
  .bullets__item {
    margin-bottom: 24px;
    height: 89px;
  }
}
.bullets__item:before {
  content: '';
  display: block;
  width: 151px;
  height: 151px;
  border: 12px solid #9e4532;
  border-radius: 50%;
  position: absolute;
}
@media (max-width: 899px) {
  .bullets__item:before {
    border-width: 4px;
    width: 89px;
    height: 89px;
  }
}
.bullets__text {
  position: relative;
  background: #fff;
  margin-left: 81px;
  bottom: 0;
  padding: 0px 10px;
  font-family: 'Franklin Gothic Demi Cond', sans-serif;
  font-size: 25px;
  color: #363940;
}
@media (max-width: 899px) {
  .bullets__text {
    font-size: 18px;
    margin-left: 45px;
  }
}
.bullets__text b {
  font-size: 37px;
  color: #902914;
}
@media (max-width: 899px) {
  .bullets__text b {
    font-size: 27px;
  }
}
.footer__disclaimer {
  font-size: 16px;
  text-align: center;
  color: #191919;
  margin-bottom: 12px;
}
@media (max-width: 899px) {
  .footer__disclaimer {
    font-size: 9px;
  }
  .footer__disclaimer br {
    display: none;
  }
}
.footer__container {
  background: #2d2d2d;
}
.footer__links {
  height: 43px;
  align-items: center;
}
@media (max-width: 899px) {
  .footer__links {
    flex-direction: column;
    justify-content: center;
    height: auto;
    padding: 4px 0;
  }
}
.footer__text {
  color: #fff;
  font-size: 15px;
  margin-right: auto;
}
@media (max-width: 899px) {
  .footer__text {
    font-size: 12px;
    text-align: center;
    margin-right: 0;
  }
}
.footer__link {
  color: #fff;
  font-size: 15px;
  margin-left: 14px;
}
.footer__link:hover {
  text-decoration: underline;
}
@media (max-width: 899px) {
  .footer__link {
    font-size: 12px;
    margin-top: 6px;
  }
}
.popup {
  margin: 0 auto;
}
.modal {
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  background: #fff;
  padding: 22px 30px;
}
@media (max-width: 899px) {
  .modal {
    padding: 8px 12px;
  }
}
.modal__title {
  font-family: 'Franklin Gothic Demi Cond', sans-serif;
  font-size: 25px;
  text-align: center;
  margin-bottom: 31px;
}
.modal__text {
  text-align: center;
  font-size: 16px;
}
